import { Component } from '@angular/core';
import { MdSliderChange } from '@angular/material';
declare let jsPDF;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';

  thumbMoved(e: MdSliderChange) {
    e.source.value = 5;
  }

  generatePdf(e) {
    console.log(e);
    const pdf = new jsPDF();

    pdf.addHTML(e, () => {
      pdf.save('myfile.pdf');
    });
  }
}



